import { useEffect, useState } from 'react'
import ReactDOM from 'react-dom'
import { Select } from 'react-select-virtualized'
import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'

const items = [
	{ value: 1, label: 'John', cat: 'white' },
	{ value: 2, label: 'Paul', cat: 'white' },
	{ value: 5, label: 'Johnson', cat: 'white' },
	{ value: 7, label: 'Paulson', cat: 'black' },
	{ value: 9, label: 'Patrick', cat: 'black' },
]

const propertyFirst = 'cat'
const propertySecond = 'label'
const propertyThird = 'value'

const changeFirst = data => {
	if (data) {
		// console.log(data)
		const unique = [
			...new Set(
				data
					.sort((a, b) => b[propertyFirst] - a[propertyFirst])
					.map(item => item[propertyFirst])
			),
		]
		// console.log(unique)
		return unique.sort()
	} else {
		// console.log('No data for First combo...')
		return []
	}
}

const changeSecond = (data, valueFirst) => {
	if (valueFirst) {
		// console.log('Changing Second combo...')
		const filteredFirst = data.filter(
			item => item[propertyFirst] === valueFirst
		)
		const unique = [...new Set(filteredFirst.map(item => item[propertySecond]))]
		// console.log(unique)
		return unique.sort()
	} else {
		return []
	}
}

export default function App() {
	const [valueFirst, setValueFirst] = useState(null)
	const [valueSecond, setValueSecond] = useState(null)
	const [valueThird, setValueThird] = useState(null)
	const [items, setItems] = useState([])
	const [itemsFirst, setItemsFirst] = useState([])
	const [itemsSecond, setItemsSecond] = useState([])
	const [itemsThird, setItemsThird] = useState([])
	const [updated, setUpdated] = useState(null)

	useEffect(() => {
		if (items) {
			setItemsFirst(changeFirst(items))
			console.log('items first:', changeFirst(items))
		} else {
			console.log('No data for itemsFirst')
		}
	}, [items])

	useEffect(() => {
		if (valueFirst) {
			setItemsSecond(changeSecond(items, valueFirst))
		} else {
			setItemsSecond([])
		}
	}, [items, valueFirst])

	let filtered = items

	if (valueSecond) {
		filtered = items
			.filter(item => item[propertyFirst] === valueFirst)
			.filter(item => item[propertySecond] === valueSecond)
	} else if (valueFirst) {
		filtered = items.filter(item => item[propertyFirst] === valueFirst)
	}

	return (
		<div className='App'>
			<h1>React Select</h1>
			<Select options={itemsFirst} />
			{items && (
				<Autocomplete
					id='combo-box-first'
					options={itemsFirst}
					getOptionLabel={option => option}
					value={valueFirst}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`first: ${value}`)
						if (value) {
							// console.log('changing first value', value)
							setValueFirst(value)
							setValueSecond(null)
							setValueThird(null)
						} else {
							// console.log('no first value...')
							setValueFirst(null)
							setValueSecond(null)
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Judetul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
		</div>
	)
}
